
.todo-container {
    margin:auto;
    margin-top:80px;
    width: 500px;
    text-align: center
   
}

@media (max-device-width: 480px) {
    .todo-container{
        margin:0;
        padding:10px;
        display:block;
    } 

    .input-section {
        width: 250px;
        height:200px;
      
    }
    .input-section input {
    width:80%;
    }

    ul li {
        width:310px;
    }

}

h1 {
    margin-bottom: 30px;
}

.input-section {
    background: #fff;
    border-radius: 5px;
    box-shadow: 0px 2px 10px 1px rgba(65, 64, 64, 0.301);
    padding: 20px 50px;
}

.input-section input {
    width:100%;
    height:30px;
    display: block;
    outline: none;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.137);
    font-size: 1.1em;

}

ul {
   padding: 0;
    text-align: left;
   
}

ul li {
    display: flex;
    list-style: none;
    background: #fff;
    padding: 20px;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0px 2px 10px 1px rgba(65, 64, 64, 0.301);
    
}

span {
    flex:1;
    text-align: right;
}

span i {
   padding: 5px 5px;
}

span i:hover {
    cursor: pointer;
}

.clear {
    padding:10px 15px;
    background: white;
    border-radius: 5px;
    cursor: pointer;
}